body {
  overflow-x: none;
}

.worldStats {
  color: rgb(75, 75, 75);
  position: absolute;
  left: 50%;
  transform: translate(-50%);
  text-align: center;
}

.navbar {
  position: sticky;
}

nav img {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}
nav hr {
  width: 100%;
}

.select-wrapper {
  margin-left: 3%;
  margin-right: 3%;
}

.center {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  text-align: center;
}

.confirmed h2 {
  color: rgb(126, 126, 126);
}
.confirmed h1 {
  color: rgb(2, 88, 138);
}

.active h3 {
  color: rgb(126, 126, 126);
}
.active h1 {
  color: rgb(97, 3, 126);
}

.deaths h3 {
  color: rgb(126, 126, 126);
}
.deaths h1 {
  color: rgb(141, 1, 1);
}

.recovered h2 {
  color: rgb(126, 126, 126);
}
.recovered h1 {
  color: rgb(3, 121, 38);
}

.hero-image {
  position: center;
}

.confirmed-u h2 {
  color: rgb(126, 126, 126);
}
.confirmed-u h1 {
  color: rgb(74, 107, 109);
}

.active-u h3 {
  color: rgb(126, 126, 126);
}
.active-u h1 {
  color: rgb(109, 109, 109);
}

.deaths-u h3 {
  color: rgb(126, 126, 126);
}
.deaths-u h1 {
  color: rgb(112, 57, 57);
}

.recovered-u h2 {
  color: rgb(126, 126, 126);
}
.recovered-u h1 {
  color: rgb(78, 55, 80);
}

.form-group {
  margin-left: 5%;
  margin-right: 5%;
}

.form-control {
  font-size: 18px;
  overflow: none;
}

.header {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  text-align: center;
}

button {
  margin: 0;
  position: absolute;
  left: 40%;
  transform: translateY(-50%);
}

a,
a:hover,
a:active {
  text-decoration: none;
}

a:hover {
  color: white;
}

.newsdiv a {
  color: rgb(255, 255, 255);
  -webkit-text-stroke: 0.5px rgb(94, 94, 94);
}
